.header {
    display: flex;
    flex-direction: row;
}

.menu {
    justify-content: stretch;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
}

.menuBlock {
    margin: 1em .25em .25em .25em;
}
.menuItem {
    padding: 1em;
    background: transparent;
    color: #000;
    font-weight: bold;
    text-decoration: none;
    font-family: inherit;
    border-radius: 3px;
}

.menuItem:hover {
    background: #c1c1c1;
}

.menuItem:focus {
    outline: .125em dotted #747474;
    outline-offset: -.125em;
}
.menuButton {
    padding: 1em;
    background: transparent;
    color: #000;
    font-weight: bold;
    text-decoration: none;
    font-family: inherit;
    border-radius: 3px;
}
.menuButton:hover {
    background: #c1c1c1;
}

.menuButton:focus {
    outline: .125em dotted #747474;
    outline-offset: -.125em;
}
.menuHidden {
    visibility: hidden;
    display: none;
}


